<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;height:675px;'">
        <h1 class="main-title">电影推荐</h1>
        <Row v-if="list.length>0">
            <Col class="index-list" v-for="(item,index) in list" :key="item.id" :xs="{ span: 24, offset:0
             }" :lg="{ span: 6, offset: index%3===0?0:3 }">
                <img class="film-img" :src="item.picSrc | imagePath">
                <h3>{{item.title}}<Button @click="toBook(item)" class="index-list-btn" type="primary">订票</Button></h3>
            </Col>
        </Row>
        <Row v-else>
            <Col class="no-data-container" :xs="{ span: 24, offset:0}" :lg="{ span: 16, offset: 4 }">
                <img class="no-data" :src="noData">
                <h2>暂无推荐电影哟</h2>
            </Col>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "index",
        data(){
          return {
	          bg:require('../../assets/frontend/index-bg.jpg'),
            list:[],
            noData:require('../../assets/frontend/common/no-data.png'),
          };
        },
        created(){
          this.initData();
        },
        methods:{
            initData(){
              var self=this;
              requestObj({
                  url:'getUserRecommend.do',
                  method:'GET',
                  params:{
                      pageCurrent:1,
                      pageSize:10,
                  },
                  success:function (res){
                      var data=res.data.data;
                      self.list=data;
                  },
                  fail:function (err){
                      console.log(err);
                  },
              });
            },
            toBook(item){
              this.$router.push('/book/'+item.id);
            },
        },
    }
</script>

<style scoped>
    .no-data-container{
        text-align: center;
    }
    .film-img{
        width:100%;
        height:200px;
    }
</style>


